.slider__title {
    position: relative;
    font-size: 48px;
    line-height: 1.2;
    font-weight: 900;
    margin-top: 10px;
}

.slider__title:after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    right: 0;
    width: 100%;
    height: 4px;
    background: #000;
}